/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';

/**
 * タブ項目
 */
registerBlockType(metadata.name, {
	title: __('Tab content', 'arkhe-blocks'),
	icon: blockIcon,
	edit: () => {
		const blockProps = useBlockProps({
			className: 'arkb-tabBody__content ark-keep-mt--s ark-has-guide',
		});

		const innerBlocksProps = useInnerBlocksProps(blockProps, {
			template: [['core/paragraph']],
			templateLock: false,
		});

		return <div {...innerBlocksProps} />;
	},

	save: ({ attributes }) => {
		const { tabId, bodyId, activeTab } = attributes;
		const blockProps = useBlockProps.save({
			className: 'arkb-tabBody__content ark-keep-mt--s',
			id: `tab-${tabId}-${bodyId}`,
			role: 'tabpanel',
			'aria-hidden': activeTab === bodyId ? 'false' : 'true',
		});

		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
});
